import React, { FormEvent, useState } from 'react';
import { useDispatch } from 'react-redux';
import styles from './Students.module.css';

export default function StudentCreation(): JSX.Element {
  const [firstName, setFirstName] = useState<string>('');
  const [lastName, setLastName] = useState<string>('');
  const [group, setGroup] = useState<string>('');
  const [averagePoint, setAveragePoint] = useState<number>(0);
  const today = new Date().toISOString().substring(0, 10);
  const [birthDate, setBirthDate] = useState<string>(today);
  const [error, setError] = useState<string>('');

  const dispatch = useDispatch();
  function clearInputsAndError(): void {
    setFirstName('');
    setLastName('');
    setGroup('cohort-26');
    setAveragePoint(0);
    setBirthDate(today);
    setError('');
  }

  function validateInputs(): boolean {
    if (firstName.trim() === '') {
      setError('FirstName is required field');
      return false;
    }
    if (lastName.trim() === '') {
      setError('LastName is required field');
      return false;
    }
    if (averagePoint === 0) {
      setError('AveragePoint is required field');
      return false;
    }
    if (birthDate === today) {
      setError('BirthDate is required field');
      return false;
    }
    return true;
  }

  function handleSubmit(e: FormEvent<HTMLFormElement>): void {
    e.preventDefault();
    if (validateInputs()) {
      dispatch({
        type: 'students/add',
        payload: {
          firstName, lastName, group, averagePoint, birthDate
        }
      });
      clearInputsAndError();
    }
  }
  // '', 0, undefined, null, NaN -> false
  return (
    <div>
      <h1>  </h1>
      <form onSubmit={handleSubmit} className={styles.container}>
        {error && (<div style={{ color: 'red' }}>{error}</div>)}
        <input type="text" placeholder="firstName" value={firstName} onChange={(e) => setFirstName(e.target.value)} />
        <input type="text" placeholder="lastName" value={lastName} onChange={(e) => setLastName(e.target.value)} />
        <select name="group" value={group} onChange={(e) => setGroup(e.target.value)}>
          <option value="" disabled selected>group</option>
          <option value="cohort-21">cohort-21</option>
          <option value="cohort-22">cohort-22</option>
          <option value="cohort-23">cohort-23</option>
          <option value="cohort-24">cohort-24</option>
          <option value="cohort-25">cohort-25</option>
          <option value="cohort-26">cohort-26</option>
        </select>
        <input type="text" placeholder="averagePoint" value={averagePoint} onChange={(e) => setAveragePoint(Number(e.target.value))} />
        <input type="date" value={birthDate} onChange={(e) => setBirthDate(e.target.value)} />
        <button type="submit"></button>
      </form>
    </div>
  );
}
