import React, { FormEvent, useState } from 'react';
import EditIcon from '@mui/icons-material/Edit';
import { useDispatch } from 'react-redux';

interface Props {
  id: string;
}

export default function StudentEdit(props: Props): JSX.Element {
  const { id } = props;
  const [firstName, setFirstName] = useState<string>('');
  const [lastName, setLastName] = useState<string>('');
  const [group, setGroup] = useState<string>('');
  const [averagePoint, setAveragePoint] = useState<number>(0);
  const [toggle, setToggle] = useState<boolean>(false);
  const dispatch = useDispatch();
  function handleEdit(event: FormEvent<HTMLFormElement>): void {
    event.preventDefault();
    dispatch({
      type: 'students/edit',
      payload: {
        id, firstName, lastName, group, averagePoint
      }
    });
    setToggle(!toggle);
    setFirstName('');
    setLastName('');
    setGroup('');
    setAveragePoint(0);
  }
  return (
    <div>
      <EditIcon onClick={() => { setToggle(!toggle); }} />
      {toggle &&
        (
          <form onSubmit={handleEdit}>
            <input type="text" placeholder="firstName" value={firstName} onChange={(e) => setFirstName(e.target.value)} />
            <input type="text" placeholder="lastName" value={lastName} onChange={(e) => setLastName(e.target.value)} />
            <select name="group" value={group} onChange={(e) => setGroup(e.target.value)}>
              <option value="" disabled selected>group</option>
              <option value="cohort-21">cohort-21</option>
              <option value="cohort-22">cohort-22</option>
              <option value="cohort-23">cohort-23</option>
              <option value="cohort-24">cohort-24</option>
              <option value="cohort-25">cohort-25</option>
              <option value="cohort-26">cohort-26</option>
            </select>
            <input type="number" placeholder="averagePoint" value={averagePoint} onChange={(e) => setAveragePoint(Number(e.target.value))} />
            <button type="submit"></button>
          </form>
        )}
    </div>
  );
}
