import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import DeleteIcon from '@mui/icons-material/Delete';
import { RootState } from '../../store';
import StudentEdit from './StudentEdit';
import styles from './Students.module.css';

export default function Students(): JSX.Element {
  const students = useSelector((state: RootState) => state.students);
  const dispatch = useDispatch();
  const handleDelete = (id: string): void => {
    dispatch({ type: 'stuidents/remove', payload: id });
  };
  return (
    <div>
      <h1>Students list</h1>
      <ul className={styles.container}>
        {students.map((student) => (
          <li key={student.id} className={styles.card}>
            <p className={styles.primaryInfo}>{student.firstName} {student.lastName}</p>
            <span className={styles.secondaryInfo}>
              <p>Birth date: {student.birthDate}</p>
              <p>average point: {student.averagePoint}</p>
              <p>group: {student.group}</p>
            </span>
            <div className={styles.control}>
              <StudentEdit id={student.id} /><DeleteIcon onClick={() => handleDelete(student.id)} />
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}
