// eslint-disable-next-line import/no-extraneous-dependencies
import { uid } from 'uid';
import Action from './types/Action';
import TasksState from './types/TasksState';

const initialValue: TasksState = [
  {
    id: uid(32), title: 'learn React', description: 'learn redux, css module...', isDone: false
  }
];

export default function tasksReducer(
  state: TasksState = initialValue,
  action: Action
): TasksState {
  switch (action.type) {
    case 'tasks/add': {
      return [...state, { ...action.payload, id: uid(32), isDone: false }];
      break;
    }
    case 'tasks/changeStatus': {
      return state.map(
        (task) => (task.id === action.payload ? { ...task, isDone: !task.isDone } : task)
      );
      break;
    }
    case 'tasks/remove': {
      return state
        .filter((task) => task.id !== action.payload);
      break;
    }

    default: return state;
      break;
  }
}
