import Action from './types/Action';
import SandwichState from './types/SandwichState';

const initialValue: SandwichState = {
  value: ':'
};

export default function sandwichReducer(
  state: SandwichState = initialValue,
  action: Action
): SandwichState {
  switch (action.type) {
    case 'sandwich/add': {
      return { value: state.value + action.payload };
      break;
    }
    case 'sandwich/clear': {
      return { value: initialValue.value };
      break;
    }

    default: return { value: state.value };
      break;
  }
}
