// eslint-disable-next-line import/no-extraneous-dependencies
import { uid } from 'uid';
import Action from './types/Action';
import Student from './types/Student';

const initialState: Student[] = [
  {
    id: uid(32),
    firstName: 'Peter',
    lastName: 'Muster',
    group: 'cohort-26',
    averagePoint: 4.4,
    birthDate: '1984-11-01',
  }
];

export default function studentsReducer(
  state: Student[] = initialState,
  action: Action
): Student[] {
  switch (action.type) {
    case 'students/add': {
      return [...state, { ...action.payload, id: uid(32) }];
      break;
    }
    case 'students/edit': {
      return state.map(
        (student) => (student.id === action.payload.id
          ? {
            ...student,
            firstName: action.payload.firstName,
            lastName: action.payload.lastName,
            group: action.payload.group,
            averagePoint: action.payload.averagePoint
          } : student)
      );
      break;
    }
    case 'students/remove': {
      return state
        .filter((student) => student.id !== action.payload);
      break;
    }

    default: return state;
      break;
  }
}
